body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.headerFont {
  font-family: 'Kaushan Script', cursive;
  color: 'black';
  padding: 50px;
  margin: 10px;
 
}


.link {
  color: #4d4e4f;
  font-family: 'Kaushan Script', cursive;
  font-size: 30px;
  
}
.link:hover {
   color: black;
   text-decoration: none;
   font-size: 40px;
}
.centerText {
  text-align: center;
  padding-top: 14px;
  padding-right: 5px;
  font-size: 30px;
}

.headerText {
  width: 100%;
  padding: 0px;
  text-align: center;
  margin-right: 10px;
  font-size: 30px;

}
.name {
  font-size: 55px;
  width: 100%;
  padding: 0px;
  text-align: center;
  margin-right: 10px;
  
}
.container {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  
}
.nav-bar{
  background-color: #d8dde3;
  opacity: 0.7
}
.image-size{
  width: 100%;
  height: 55%
}